import React, { useState } from "react";
import "../styles/Certifications.css";

const certificates = [
    { id: 1, title: "Introduction to JAVA", file: process.env.PUBLIC_URL + "/certs/IntroToJava.jpeg" },
    { id: 2, title: "Data Structures in JAVA", file: process.env.PUBLIC_URL + "/certs/DS.jpeg" },
    { id: 3, title: "IBM Sterling Order Management", file: process.env.PUBLIC_URL + "/certs/OMoC.jpeg" },
    { id: 4, title: "Self Service Tool", file: process.env.PUBLIC_URL + "/certs/SST.jpeg" },
    { id: 5, title: "Developer Toolkit", file: process.env.PUBLIC_URL + "/certs/toolkit.jpeg" }
];

const Certifications = () => {
    const [selectedCert, setSelectedCert] = useState(null);

    return (
        <div className="certifications-container">
            <h2 className="certifications-title">My Certifications</h2>
            <p className="certifications-subtext">Click on a certification to view the full document.</p>
            
            {/* Grid Layout for Certificates */}
            <div className="certifications-grid">
                {certificates.map((cert) => (
                    <div key={cert.id} className="cert-card" onClick={() => setSelectedCert(cert.file)}>
                        <img src={cert.file} alt={cert.title} className="cert-thumbnail" />
                        <p className="cert-title">{cert.title}</p>
                    </div>
                ))}
            </div>
            
            {/* Modal for Full View */}
            {selectedCert && (
                <div className="cert-modal-overlay" onClick={() => setSelectedCert(null)}>
                    <div className="cert-modal" onClick={(e) => e.stopPropagation()}>
                        <img src={selectedCert} alt="Certification" className="cert-modal-img" />
                        <button className="close-btn" onClick={() => setSelectedCert(null)}>✖</button>
                    </div>
                </div>
            )}
        </div>
    );
};

export default Certifications;
